<template>
    <section>
        <v-text-field
        ref="nombre"
        label="Nombre de la escuela"
        prepend-icon="mdi-town-hall"
        v-model="escuela.nombre"
        ></v-text-field>
        <v-text-field
        label="Título obtenido"
        prepend-icon="mdi-school"
        v-model="escuela.titulo"
        ></v-text-field>
        <v-text-field
        label="Periodo estudiado"
        prepend-icon="mdi-calendar"
        v-model="escuela.periodo"
        ></v-text-field>
        <v-text-field
        label="Lugar"
        prepend-icon="mdi-google-maps"
        v-model="escuela.lugar"
        ></v-text-field>
        <v-btn
          block
          dark
          :color="color"
          elevation="2"
          @click="agregarEscuela"
        >
            <span v-if="editar">Aceptar</span>
            <span v-if="!editar">Añadir</span>
        </v-btn>        
        <div v-if="escuelas.length > 0">
            <tabla-datos
            :encabezado="encabezadoTabla" 
            :datos="escuelas"
            @editar="editarEscuela"
            @eliminar="eliminarEscuela" 
            />   
        </div>
    </section>
</template>
<script>
    import TablaDatos from './TablaDatos'
    export default{
        name: "DatosEducacion",
        components: { TablaDatos },

        data: ()=>({
            encabezadoTabla: [
                {
                  text: "Escuela",
                  value: "nombre",
                },
                {
                  text: "Título",
                  value: "titulo",
                },
                {
                  text: "Periodo",
                  value: "periodo",
                },
                {
                  text: "Lugar",
                  value: "lugar",
                },
                {
                  text: "Opciones",
                  value: "opciones",
                },
            ],
            escuelas: [],
            escuela:{
                nombre: "",
                titulo: "",
                periodo: "",
                lugar:""
            },
            editar: false,
            indice: -1,
            color: "indigo darken-4"
        }),

        mounted(){
            this.ponerFocus()
        },

        methods: {
            eliminarEscuela(escuela){
                this.escuelas.splice(this.escuelas.indexOf(escuela), 1)
            },

            editarEscuela(escuela){
                this.color = "secondary"
                this.editar = true
                this.escuela = escuela
                this.indice = this.escuelas.indexOf(escuela)
            },

            agregarEscuela(){
                if(this.indice > -1){
                    Object.assign(this.escuelas[this.indice], this.escuela)
                    this.editar = false
                    this.color = "indigo darken-4"
                    this.indice = -1
                } else {
                    this.escuelas.push(this.escuela)
                }
                
                this.emitir()
                this.escuela = {
                    nombre: "",
                    titulo: "",
                    periodo: "",
                    lugar:""
                }
                this.ponerFocus()
            },

            emitir(){
                this.$emit("escuela", this.escuelas)
            },

            ponerFocus(){
                this.$refs["nombre"].focus()
            }   
        }
    }
</script>