<template>
    <section>
        <v-text-field
        ref="nombreIdioma"
        label="Idioma"
        v-model="idioma.nombre"
        prepend-icon="mdi-translate"
        ></v-text-field>
        <v-text-field
        label="Porcentaje"
        v-model="idioma.porcentaje"
        prepend-icon="mdi-numeric"
        ></v-text-field>
        <v-color-picker
        dot-size="25"
        v-model="idioma.color"
        swatches-max-height="200"
        ></v-color-picker>

         <v-btn
          block
          dark
          :color="color"
          elevation="2"
          @click="agregarIdioma"
        >
            <span v-if="editar">Aceptar</span>
            <span v-if="!editar">Añadir</span>
        </v-btn>
         <div v-if="idiomas.length > 0">
            <tabla-datos
            :encabezado="encabezadoTabla" 
            :datos="idiomas"
            @editar="editarIdioma"
            @eliminar="eliminarIdioma" 
            />   
        </div>
    </section>
</template>
<script>
    import TablaDatos from './TablaDatos'
    export default{
        name: "IdiomasComponent",
        components: { TablaDatos },
        data: ()=>({
            encabezadoTabla: [
                {
                  text: "Idioma",
                  value: "nombre",
                },
                {
                  text: "Porcentaje",
                  value: "porcentaje",
                },
                {
                  text: "Color",
                  value: "color",
                },
                {
                  text: "Opciones",
                  value: "opciones",
                },
            ],
            idiomas: [],
            idioma: {
                nombre: "", 
                porcentaje: "",
                color: ""
            },
            color: "indigo darken-4",
            editar: false,
            indice: -1,
        }),

        methods: {
            eliminarIdioma(idioma){
                this.idiomas.splice(this.idiomas.indexOf(idioma), 1)
            },

            editarIdioma(idioma){
                this.color = "secondary"
                this.editar = true
                this.idioma = idioma
                this.indice = this.idiomas.indexOf(idioma)
            },

            agregarIdioma(){
                 if(this.indice > -1){
                    Object.assign(this.idiomas[this.indice], this.idioma)
                    this.editar = false
                    this.color = "indigo darken-4"
                    this.indice = -1
                } else {
                    this.idiomas.push(this.idioma)
                }
                this.emitir()
                this.idioma = {
                    nombre: "",
                    porcentaje: "",
                    color: ""
                }
                this.ponerFocus()
            },

            emitir(){
                this.$emit("idioma", this.idiomas)
            },

            ponerFocus(){
                this.$refs["nombreIdioma"].focus()
            }
        }
    }
</script>