import Vue from 'vue'
import VueRouter from 'vue-router'
import CrearCurriculum from '../components/CrearCurriculum'
import AcercaDe from '../components/AcercaDe'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'crear',
    component: CrearCurriculum
  }, 
  {
    path: '/acerca-de',
    name: 'acerca',
    component: AcercaDe
  },

]

const router = new VueRouter({
  routes
})

export default router
